@use '../../../sharedfc/input' as Input;
@use 'utilities';
@use 'tools';

:host {
  width: 100%;
  color: var(--ath-color-fg-default);
  font-family: var(--ath-font-family-primary);
  @include Input.fc-input-helper;
  @include Input.fc-input-feedback;
  @include Input.fc-input-label;

  .ath-radiobutton-group {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: var(--ath-spacing-between-100);
    border: none;
    margin: 0;
    padding: 0;

    .ath-radiobutton__group {
      width: 100%;
      display: flex;
      align-items: flex-start;
      flex-direction: column;
      gap: var(--ath-spacing-radiobutton-group-row-gap);
      align-self: stretch;
      &-horizontal {
        flex-direction: row;
        gap: var(--ath-spacing-between-300);
      }
    }

    .sr-only {
      position: absolute;
      width: 1px;
      height: 1px;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      white-space: nowrap;
      border: 0;
    }
  }
}
